import React, { Component } from 'react';

function fetch() {
    return new Promise(resolve => setTimeout(() => resolve(42), 1000));
}

function fetchAPI(param) {
    // param is a highlighted word from the user before it clicked the button
    return fetch(param);
}

class Card extends Component {
    constructor(props) {
        super(props);
        this.snap = props.snap;
        this.name = props.name;
        this.src = props.src;
        this._id = props._id;
        this.CId = props._id+"0";
        this.location = props.location;
    }
    show(containerId,imageSource, imageId) {
        try{
            var elementToBeRemoved = document.getElementById(imageId);
            elementToBeRemoved.parentNode.removeChild(elementToBeRemoved);
            }
            catch(e){
                console.log(e);
            }
        try{
        var img = document.createElement("IMG");
        img.src = imageSource;
        img.setAttribute('id', imageId);
        img.setAttribute('width', "400px");
        img.setAttribute('height', "400px");
        document.getElementById(containerId).appendChild(img);
        console.log(img);
        return img;
        }
        catch(e){
            console.log(e);
        }
    }
    state = { result: null };

    toggleButtonState = () => {
        fetchAPI(this.snap).then(result => {
            this.setState({ result });
        });
    };

    
    hide(imageId) {
        try{
        var elementToBeRemoved = document.getElementById(imageId);
        elementToBeRemoved.parentNode.removeChild(elementToBeRemoved);
        }
        catch(e){
            console.log(e);
        }
    }
    
    render() {
        return (
            <div id={this.CId} className='bg-dark-gray dib-ns br3 pa3 ma2  bw2 shadow-5'>
                {/* <img id={this._id} src={this.src} width="400px" height="400px"></img> */}
                <div >
                    <h1>{this.name}</h1>
                    <h2 className='white'>{this.location}</h2>
                    <a
                        href={this.snap}
                        download
                        target="_blank"
                    >Take a Snap</a>
                    <div>
                    <button onClick={() => this.show(this.CId,this.src,this._id)}>Show</button> 
                    <button onClick={() => this.hide(this._id)}>Hide</button> 
                    </div>
                </div>
                </div>
        );
    }
}
export default Card;
